import logo from './logo.svg';
import './App.css';
import {Route, Switch} from "react-router";

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact={true} path={'/'} component={() => { return 'home'}} />
        <Route exact={true} path={'/about'} component={() => { return 'about'}} />
        <Route exact={true} path={'/blog'} component={() => { return 'blog'}} />
      </Switch>
    </div>
  );
}

export default App;
